import React, { Component } from 'react';
import { useRemixController } from '@remix/core/hooks';

import evalute from '../../evalute';

import './index.css';



class Playground extends Component {
  static getDerivedStateFromProps () {
  }

  state = {
    value: 100
  }

  onTap = () => {
    const { value } = this.state;

    this.setState({
      value: value + 100
    })
  }

  render () {
    const { value } = this.state;

    return (
      <view className="playground">
        <view className="playground__title">{value}</view>
        <view className="playground__ctrl">
          <view className="playground__ctrl-button playground__parse">
            Parse
          </view>
          <view className="playground__ctrl-button playground__step">
            Step
          </view>
          <view className="playground__ctrl-button playground__run" onTap={this.onTap}>
            Run
          </view>
        </view>
      </view>
    );
  }
}

export default useRemixController(Playground, {
  backgroundColor: '#efefed'
});